<template>
  <div>


    <van-nav-bar title="订单列表" left-arrow @click-left="onClickLeft()" />



    <van-tabs v-model="active" @click="tabSelect">
      <van-tab title="全部" />
      <van-tab title="待付款" />
      <van-tab title="待发货" />
      <van-tab title="已发货" />
      <van-tab title="待评价" />
    </van-tabs>

    <van-empty v-if="order_list.length==0" image="error" description="描述文字" />

    <van-pull-refresh v-model="isLoading" @refresh="onRefresh"  v-else style="height: 85vh; overflow: auto;">
      <van-cell-group v-for="(item,index) in order_list" :key="index">
        <van-cell title="订单编号：16299688165764" value="待付款"></van-cell>
        <van-card thumb="https://img01.yzcdn.cn/vant/ipad.jpeg">
          <template #title>
            <van-cell style="padding: 0px;">
              <van-col span="20">商品标题</van-col>
              <van-col span="4" style="text-align:right;">2000</van-col>
            </van-cell>
          </template>
          <template #desc>
            <van-cell style="padding: 0px;margin-top: 20px;">
              <van-col span="24" style="text-align: right;">x2</van-col>
            </van-cell>
          </template>
          <template #footer>
            <van-row style="text-align: left;">
              <span>合计<b>￥2000</b></span>
              <span>共<b>1</b>件</span>
            </van-row>
            <van-row style="padding: 10px; text-align: right;">
              <van-button plain type="info" size="mini" color="#999" @click="navTo('order_info',1)">查看详情</van-button>
              <van-button plain type="info" size="mini" color="#000" @click="navTo('payment',1)">立即支付</van-button>
            </van-row>
          </template>
        </van-card>
      </van-cell-group>



    </van-pull-refresh>



  </div>
</template>

<script>
export default {
  name: "orderList",
  data() {
    return {
      active: 0,
      count: 0,
      isLoading: false,
      order_list:[
      ],
    }
  },
  mounted() {
    console.log(this.$route.params.id);
    if(this.$route.params.id!=undefined){
      this.active=this.$route.params.id
    }
  },
  methods: {
    onRefresh() {
      setTimeout(() => {
        this.order_list.push({id:2})
        this.isLoading = false;
        this.count++;
      }, 1000);
    },
    //获取数据
    getDataList(){
      if(this.active==0){
        this.order_list=[
          {id:1}
        ]
      }else{
        this.order_list=[];
      }
    },
    //点击跳转
    navTo(name,id){
      this.$router.push({name:name,params:{id:id,url:'order'}})
    },
    //选择tab
    tabSelect(){
      this.getDataList();
    },
    onClickLeft() {
      this.$router.go(-1);
    },
  }
}
</script>

<style scoped>

</style>



